@mixin buttonStyle {
	margin: 2px;
	border: 2px solid #000;
	border-radius: 6px;
	padding: 6px;
	background-color: #fff;
	&:hover {
		background-color: #efe;
	}
	&:disabled {
		background-color: #ccc;
		color: #999;
		border-color: #999;
		img {
			opacity: 0.3;
		}
	}
	&[aria-pressed=true] {
		border-color: #33f;
		background-color: #eef;
	}
}

@mixin hoverShadow($color) {
	&:hover {
		box-shadow: inset 0 0 8px $color;
	}
}

@mixin licenseBorderShared {
	box-shadow: inset 0 0 2px #000;
}

@mixin licenseShared {
	cursor: pointer;
	&.obtained {
		background-color: rgb(250, 190, 78);
		@include hoverShadow(rgb(255, 72, 0));
	}
	&.certain {
		background-color: rgb(248, 219, 165);
		@include hoverShadow(rgb(110, 184, 25));
	}
	&.possible {
		background-color: rgb(204, 149, 77);
		@include hoverShadow(rgb(110, 184, 25));
	}
	&.blocked {
		color: white;
		background-color: rgb(160, 68, 32);
		@include hoverShadow(rgb(255, 72, 0));
		cursor: not-allowed;
	}
	&.unreachable {
		@include hoverShadow(rgb(255, 72, 0));
		cursor: not-allowed;
	}
	&.planned {
		background-color: rgb(201, 224, 68);
	}
}
